.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  position: relative;
}
.user-time {
  position: absolute;
  top: 20px;
  left: 20px;
  color: black;
}
.edit {
  position: absolute;
  top: 20px;
  right: 20px;
  color: black;
  z-index: 1;
}

.cardActions {
  padding: 0 16px 8px 16px;
  display: flex;
  justify-content: space-between;
  z-index: 1;
}

.content {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);

  width: 100%;
  height: 100%;
  /* border:10px solid yellow; */
  box-sizing: border-box;

  opacity: 0;
  transition: all 1s;
  cursor: pointer;
}
.content .content-inner {
  font-family: "Nanum Myeongjo", serif;
  display: table-cell;
  text-align: left;
  vertical-align: center;
  /* border:10px solid blue; */
  color: #fff;

  padding: 150px 30px;
  z-index: 3;
}
.content:hover {
  opacity: 1;
}
