.bgContainer {
  height: 100vh;
  position: relative;
}
.background {
  width: 100%;
  height: 100%;
  overflow: hidden;
  /* background-image: url(../../img/bgbg2.png); */
  /* background-color: #f4f4f4; */
}
video {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}
.bgContent {
  z-index: 3;
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
