    display:flex;
    width:100vw;
    height: 100vh;
    
    background-image: url(../../img/image5.PNG);
    background-repeat: no-repeat;
    background-size: cover;
  }  */
.bg-text {
  background-color: #f4f4f4;

  /* border: 1px solid black; */
  width: 45vw;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 16vw;
  padding-bottom: 22vh;
  /* background-color: #eeeaea; */
  position: relative;
}
.bg-text h1 {
  text-shadow: 100px 30px 0 rgba(0, 0, 0, 0.16);
  font-family: "Old Standard TT", serif;
  /* font-weight: bold; */
  font-weight: 300;
  font-size: 4rem;
  color: #3d3d3d;
  margin: 8rem 0;
  line-height: 1.4;
}
.line {
  width: 0.5px;
  height: 20vw;
  /* border: 1px solid rgb(209, 209, 209); */
  background-color: #474747;

  position: absolute;
  bottom: 0%;
  left: 10%;
}
.circle {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: #474747;
  position: absolute;
  bottom: 20vw;
  left: 9.3%;
}
.date {
  writing-mode: vertical-rl;
  /* text-orientation: upright; */
  position: absolute;
  bottom: 46%;
  left: 2.5%;
  font-family: "Libre Baskerville", serif;
  font-size: 2.5rem;
  color: #474747;
  /* transform: rotateY(180deg); */
}
.bg-text h2 {
  font-family: "Noto Sans KR", serif;
  /* text-align: center; */
  font-size: 1.2rem;
  color: #3d3d3d;
  margin-bottom: 4rem;
  /* padding-right: 10vw; */
}
.bg-text p {
  /* text-align: center; */
  font-family: "Noto Sans KR", serif;
  font-weight: 300;
  font-size: 1rem;
  line-height: 2;
  color: #4b4b4b;
  /* padding-right: 10vw; */
}
.bg-text .button {
  width: 300px;
  height: 48px;
  border: 1px solid black;
  border-radius: 30px;
  color: black;
  text-align: center;
  position: absolute;
  top: 80%;
  left: 36%;
  cursor: pointer;
  /* text세로정렬 */
  line-height: 50px;
  transition: all 300ms ease;
}
.bg-text .button:hover {
  background-color: black;
  color: #fff;
}
.img-box {
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  /* justify-content:center; */
  align-items: center;
}
.img-box Button {
  margin-bottom: 7vh;
}
.img-slide {
  /* border: 1px solid blue; */
  position: relative;
  height: 100vh;
  width: 55vw;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* background-color: #f1f0ee; */
  /* background-color: #fff; */
  padding-left: 5vw;
}
.img-slide img {
  /* padding: 1% 5%; */
  /* height: 95vh; */
  /* width: 33%; */

  width: 500px;
  height: 700px;
  /* border-radius: 10px; */
  object-fit: contain;
}

.right-arrow {
  position: absolute;
  top: 86%;
  right: 30%;
  color: rgb(150, 150, 150);
  z-index: 10;
  cursor: pointer;
  user-select: none;
}
.left-arrow {
  position: absolute;
  top: 86%;
  left: 60%;
  font-size: 2rem;
  color: rgb(150, 150, 150);
  z-index: 10;
  cursor: pointer;
  user-select: none;
}
.img-slide .a {
  /* position: absolute;
  top: 10px;
  right: 30%; */
  writing-mode: vertical-rl;
  font-size: 1.2rem;
  font-family: "Merriweather", serif;
  letter-spacing: 2rem;
}
.img-slide .sub {
  position: absolute;
  top: 20%;
  right: 3%;
  writing-mode: vertical-rl;
  font-size: 1rem;
  font-family: "Nanum Myeongjo", serif;
  letter-spacing: 0.1rem;
}

.img-text {
  font-family: "Libre Baskerville", "Noto Sans KR", serif;
  font-size: 0.8rem;
}
.img-text1 {
  position: absolute;
  top: 55%;
  left: 67%;
}
.img-text2 {
  position: absolute;
  top: 58%;
  left: 67%;

  color: darkgray;
}
.img-text3 {
  position: absolute;
  top: 70%;
  left: 67%;
}
.img-text4 {
  position: absolute;
  top: 73%;
  left: 67%;

  color: darkgray;
}
.slide {
  opacity: 0;
  transition-duration: 1s ease;
}
.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.1);
}

.keywords {
  width: 100vw;
  height: 100vh;
  font-family: "Old Standard TT", "Noto Sans KR", serif;
  padding: 25vh 0;
}

.keywords p {
  color: darkgray;
  font-size: 14px;
}

.keywords-list {
  width: 950px;
  height: 50vh;
  margin: 60px auto 0;
  position: relative;
}

.keyword-item {
  position: absolute;
  width: 150px;
  height: 150px;
  text-align: center;
  border: 1px solid rgb(224, 224, 224);
  font-size: 14px;
  line-height: 18px;
  transition: all 300ms ease;
  z-index: 1;
}
.keyword-item .text {
  display: block;
  position: relative;
  top: 45%;
  color: rgb(53, 53, 53);
}
a:hover {
  text-decoration: none;
  border-color: #fe918d;
  transform: scale(1.1);
  z-index: 2;
}
.keyword-item :hover {
  color: #fe918d;
}
@media screen and (max-width: 768px) {
  .background {
    height: 135vh;
    background-color: #f7f5f1;
  }
  video {
    display: none;
  }
  .bgContent {
    flex-direction: column-reverse;
  }
  .bgContainer {
    position: relative;
    height: inherit;
  }

  .bg-text {
    margin: 30px 0;
    width: 100%;
    padding: 0 15vw;
  }

  .img-slide {
    width: 100%;
    height: 100vh;
    background-color: #fff;
  }
  .img-slide img {
    margin-top: 10vh;
    width: 80%;
    height: inherit;
  }
  .img-box Button {
    margin: 0;
  }
}
