.bg {
  background-image: url(./img/bg3.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  margin: 0;
  padding: 32px;
}
/* .bg-content{
  display: flex;
  align-items: center;
  justify-content: center;
} */
.bg img {
  width: 420px;
  height: 420px;
  opacity: 85%;
  border-radius: 3%;
  margin: 50px;
}

.bg Button {
  margin: 24px;
}

.bg h1 {
  text-shadow: 30px 35px 0 rgba(0, 0, 0, 0.16);
  font-family: "Nanum Myeongjo", serif;
  font-weight: bold;
  font-size: 2rem;
  line-height: 1.15;
  text-align: center;
  color: #3d3d3d;
}
.bg p {
  margin-top: 3.125em;
  font-family: "Nanum Myeongjo", serif;
  font-size: 18px;
  padding-bottom: 36px;
}

.imgB {
  margin-left: 400px;
  display: flex;
  width: 40%;
  padding: 3rem;
}

.main-banner {
  margin: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

