.post h3 {
  margin: 100px auto;
  font-family: "Nanum Myeongjo", serif;
}
.note {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.form {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.post-img {
  /* border: 1px solid red; */
  width: 50%;
  height: 100%;
  /* line-height: 100%; */
}
.post-img img {
  /* vertical-align: middle; */
  width: 700px;
  height: 700px;
  /* 비율유지contain .cover는 꽉 채워줌 */
  object-fit: contain;
  margin: 15vh;
}
.post {
  /* border: 1px solid blue; */
  width: 50%;
}
.post-write {
  margin: 0 auto;
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 80%;
  height: 60%;
  font-family: "Nanum Myeongjo", serif;
}
.btns {
  display: flex;
  width: 20%;
  justify-content: space-between;
  margin: 30px auto;
}
.btns Button {
  margin: 10px;
}

textarea {
  height: 50%;
  padding: 10px;
  line-height: 1.5;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid lightgray;
}

@media screen and (max-width: 768px) {
  .form {
    flex-direction: column;
  }
}
