  border-bottom: 1px solid #eee;
  margin: 50px auto;
}
.comment_box{
  width: 700px;
  /* border: 1px solid blue; */
  display: flex;
  justify-content:space-between;
  /* align-items: center; */
  margin: 20px auto;
}
.comment_box .comment_write{
  width: 620px;
  height: 120px;
  padding: 16px;
  /* display: none; */
  border: 1px solid #eee;
  font-size: 14px;
}
.comment_box Button{
  width: 24px;
  height: 24px;
  font-size: 16px;
  margin: 0 0 0 620px;
}

.comment_boxs{
  width: 700px;
  display: flex;
  
  margin: 50px auto;
  padding-bottom: 30px;
  border-bottom: 1px solid #eee;

  position:relative;
}
.comment_boxss{
  /* width: 700px; */
  display:flex;
  padding-bottom: 20px;
}

.comment_userName{
  font-size: 12px;
  text-align:left;
  color:darkgray;
  margin: 0 50px 16px 50px;
}
.comment{
  font-size: 16px;
  text-align: left;
  margin: 0 50px;
}
.edit_delete{
  display: flex;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  right: 30px;
}
.edit_delete p{
  margin: 10px;
  font-size: 12px;
  color:darkgray;
}
