.user {
  display: flex;
  margin: 100px 0;
}
.user img {
  border-radius: 50%;
  width: 130px;
  height: 130px;
}
.user-info {
  padding-left: 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: left;
}
.user-name {
  font-size: 24px;
  font-weight: bold;
}
